import React, { Component } from 'react';

import {
  BASE_PATH,
  SEARCH_PATH,
  SEARCH_PARAM,
  PAGE_HITS,
  PAGE_PARAM,
  HITS,
} from '../../constants/constants';
import Title from '../../components/title/title';
import Post from '../../components/post/post';
import Input from '../../components/input/input';
import Select from '../../components/select/select';
import Pagination from '../../components/pagination/pagination';

import './posts.css';

class Posts extends Component {
  state = {
    searchQuery: '',
    result: {},
    hitsPerPage: 20,
    page: 0,
  };

  componentDidMount() {
    const { searchQuery, hitsPerPage, page } = this.state;
    this.fetchData(searchQuery, hitsPerPage, page);
  }

  fetchData = (searchQuery, hitsPerPage, page) => {
    fetch(
      `${BASE_PATH}${SEARCH_PATH}?${SEARCH_PARAM}${searchQuery}&${PAGE_HITS}${hitsPerPage}&${PAGE_PARAM}${page}`,
    )
      .then(res => res.json())
      .then(result => this.setNews(result))
      .catch(error => error);
  };

  handleInputChange = ({ target: { value } }) => {
    this.setState({
      searchQuery: value,
    });
  };

  handleHitsChange = ({ target: { value } }) => {
    const { searchQuery } = this.state;
    const page = 0;

    this.setState(
      {
        hitsPerPage: +value,
        page,
      },
      () => {
        const { hitsPerPage } = this.state;

        this.fetchData(searchQuery, hitsPerPage, page);
      },
    );
  };

  getSearch = ({ key }) => {
    if (key === 'Enter') {
      const { searchQuery, hitsPerPage } = this.state;
      const page = 0;
      this.setState({
        page,
      });
      this.fetchData(searchQuery, hitsPerPage, page);
    }
  };

  setNews = result => {
    this.setState({ result });
  };

  handlePageChange = ({ target }) => {
    const btnType = target.getAttribute('data-name');
    const { page } = this.state;

    if (!Number.isNaN(btnType)) {
      this.updatePage(+btnType);
    } else {
      switch (btnType) {
        case 'next':
          this.updatePage(page + 1);
          break;
        case 'prev':
          this.updatePage(page - 1);
          break;
        default:
      }
    }
  };

  updatePage = page => {
    const { searchQuery, hitsPerPage } = this.state;
    this.setState(
      {
        page,
      },
      () => {
        this.fetchData(searchQuery, hitsPerPage, page);
      },
    );
  };

  render() {
    const { searchQuery, result, hitsPerPage } = this.state;
    const { hits = [], page, nbPages } = result;

    return (
      <div className="wrapper">
        <Title title="Hacker News" />
        <Select handleChange={this.handleHitsChange} options={HITS} value={hitsPerPage} />
        {hits.length > 0 && (
          <Pagination lastPage={nbPages} onClick={this.handlePageChange} page={page} />
        )}
        <Input onChange={this.handleInputChange} onKeyPress={this.getSearch} value={searchQuery} />
        <ul className="newsList">
          {hits.map(({ author, created_at, num_comments, objectID, title, points, url }) => (
            <Post
              key={objectID}
              author={author}
              created_at={created_at}
              num_comments={num_comments}
              points={points}
              title={title}
              url={url}
            />
          ))}
        </ul>
      </div>
    );
  }
}

export default Posts;
